.main-header {
  padding-top: 1px;
  min-height: 879px;
  background-image: url("../img/main-header__bg.jpg");
  background-repeat: no-repeat;
  background-size: 1366px 906px;
  background-position: center top; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .main-header {
      background-image: url("../img/main-header__bg@2x.jpg"); } }

.main-header__wrapper {
  margin: 0 auto;
  margin-top: 30px;
  padding: 0 17px 0 12px;
  width: 1171px; }

.main-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
  line-height: 18px; }

.main-header__logo img {
  width: 137px;
  height: 48px; }

.main-header__order-call {
  text-transform: none;
  font-variant: small-caps;
  border-bottom: 1px dashed; }

.main-header__promo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  margin-top: 455px;
  width: 350px; }
  .main-header__promo h1 {
    margin: 0;
    margin-bottom: 35px;
    font-size: 24px;
    line-height: 1.2;
    font-family: "Fira Sans", "Helvetica", sans-serif;
